<template>
  <div class="welcome">
    <div v-if="name !== ''">
      <h1 class="mb-0">Chào mừng Quý khách</h1>
      <h1 class="mb-0">{{ name }}</h1>
      <h1 class="mb-0">Đã đến tham gia chương trình.</h1>
    </div>
  </div>
</template>
<script>
export default {
  name: 'WelcomeComponent',
  data() {
    return {
      link: '',
    };
  },
  computed: {
    name() {
      if (this.link) {
        const nameRegexp = new RegExp('\\?welcome=(.*)$', 'gm');
        const match = nameRegexp.exec(this.link);
        if (match[1]) {
          return decodeURI(match[1]);
        }
        return '';
      }
      return '';
    },
  },
  created() {
    const customerRef = this.$database
      .ref('customers')
      .orderByChild('dateAdded')
      .limitToLast(1);
    customerRef.on('value', (snapshot) => {
      snapshot.forEach((childSnapshot) => {
        // const childKey = childSnapshot.key;
        const childData = childSnapshot.val();
        const { link } = childData;
        this.link = link;
      });
    });
  },
};
</script>
<style lang="scss" scoped>
.welcome {
  width: 100%;
  height: calc(100% - 156px);
  @media screen and (max-width: 1024px) {
    height: calc(100% - 150px);
  }
  color: white;
  padding: 1rem;
}
</style>
